import {Accordion} from 'react-bootstrap';
import './App.css';

function Disease(){

  return (
    <div className='disease _container'>
      <div className='inner'>   
        <Accordion defaultActiveKey={['0']} alwaysOpen>
            <Accordion.Item eventKey="0">
              <Accordion.Header>감기</Accordion.Header>
              <Accordion.Body>
                <div className='img_wrap'>
                  <img src='img/di_1.JPG'/><img src='img/di_2.JPG'/>
                </div>
                거북이의 폐는 등갑과 가깝게 위치해있고 그 아래가 거북의 장기들이 있습니다. 하지만 일반적으로 사람과 포유류들과 다르게 횡격막이 없어 자신의 장기를 쪼그려뜨리기 위해 고개를 까딱거리거나 앞다리를 흔들거리면서 바닥의 진동을 통해 공기를 폐로 끌어들이면서 호흡하며 자신의 체온조절을 하기 위함입니다. 그러나 마치 딸꾹질을 하는 듯이 고개를 휙휙 움직이거나 삑삑 소리를 낸다면 감기 증상일 수 있으니 병원에 데려가는 것을 권합니다.
                <br/><br/>
                사람은 감기에 걸리면 가래나 콧물을 뱉어낼 수 있지만 횡경막이 없는 거북이들은 그럴 수 없으니 염증이 계속 폐에 축적되고 코가 막힌채로 호흡하기가 힘들어 구강호흡을 하는 과정에서 위장에 가스가 차게됩니다. 이것이 악화되면 자연스럽게 거식증세가 나타나게되고 탈수도 오게되어서 눈이 함몰되고 나중엔 눈뜨는 것도 힘들게 되며 폐사하게 됩니다.
                <br/><br/>
                거북이가 긕긕거리는 소리를 내거나 콧물이 지속적으로 흐르며 구강 호흡을 한다면 감기가 의심되니 병원에 내원할 것을 권해드립니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="1">
              <Accordion.Header>구강호흡 , 콧망울 , 쇳소리</Accordion.Header>
              <Accordion.Body>
                이것은 호흡기 질환임을 보여주는 증상입니다.
                <br/><br/>
                호흡기 질환을 일으키는 요인은 공기 중 습도가 매우 다습하고 환기가 불량할 때, 다른 개체와 합사 시 감기가 있는 거북에게 옮거나 공기가 차가워 지속적으로 찬공기를 마셨을 때 등이 있습니다.
                <br/><br/>
                육겁의 폐 면적은 전체의 70퍼이상을 차지하고 횡격막이 없고 늑골의 유연성이 없기 때문에 숨을 들이쉬려면 장기를 수축시켜 폐가 팽창할 공간을 확보하고 앞발과 머리를 이용해 공기를 폐로 끌어들입니다.
                <br/><br/>
                그렇기 때문에 가래나 콧물이 생기면 뱉어내지 못하고 축적되어 폐의 염증이 생기게 되고 숨쉬기 어려워 구강 호흡을 하게되면 위장에 가스가 차서 거식 증상도 오게되고 수분 섭취도 힘들어 눈이 함몰되고 피부 탄력성도 잃고 활동도 줄어들어 전체적인 대사가 되지않아 소화불량, 변비 등으로 폐사하게 됩니다.
                <br/><br/>
                따뜻한 열원 제공, 충분한 환기, 위생 관리를 통한 예방이 가장 중요하고 이미 위와 같은 증상이 보인다면 질병이 상당히 진행된 상태이므로 최대한 빨리 병원에 방문하고 환경을 개선할 것을 권해드립니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="2">
              <Accordion.Header>거식</Accordion.Header>
              <Accordion.Body>
                거식의 원인으로는 잦은 핸들링으로 인한 스트레스, 사육 온도가 너무 높거나 낮은 경우, 충분하지 못한 수분공급, 사육장이 너무 불결할 경우, 한 가지 먹이만 제공하는 경우 등이 있습니다. 환경적인 문제는 시급히 개선해주고 수분의 경우는 매일 신선한 물을 먹을 수 있도록 제공해주어야합니다.
                <br/><br/>
                또한 감기로 인한 거식증상이 일어날 수 있기 때문에 건강 상태를 꼼꼼히 확인하고 한동안 먹이반응을 관찰한 뒤, 병원에 내원해야 합니다. 그러나 거북이의 경우 자발적으로 먹이활동을 하지 않으면 회복할 확률이 낮습니다. 미리 좋은 환경을 조성하여 거식을 예방하는 것이 가장 중요합니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="3">
              <Accordion.Header>장막힘(임팩션)</Accordion.Header>
              <Accordion.Body>                
                임팩션은 다양한 원인으로 발생합니다. 가장 흔한 원인은 소화가 안되는 바닥재를 삼켰을 경우입니다. 거북이가 야채를 먹으면서 바닥재가 지나치게 묻을 경우, 이 바닥재가 소화가 되지 않는 종류일 경우 장막힘이 발생할 수 있습니다. 혹은 충분한 수분섭취를 하지 못해 변비나 요석으로 인해 발생할 수 있습니다.
                <br/><br/>
                임팩션의 증상으로는 변비, 경미한 다리 떨림, 구토, 마비, 식욕부진, 복부 파란멍, 호흡곤란 등이 있습니다.
                <br/><br/>
                치료법으로는<br/>
                - 바닥재를 바꾼다.<br/>
                - 병원에 가서 초음파를 찍는다.<br/>
                - 매일 온욕을 시켜준다.<br/>
                - 바닥을 따뜻하게 해준다.<br/>
                - 거식이 오래된 경우 강제급여를 시도합니다.
                <br/><br/>
                버로우를 하는 종이나 요산을 배출하는 종은 임팩션의 위험이 있으니 미리 바닥재를 신경써서 임팩션을 예방하는 것이 중요합니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="4">
              <Accordion.Header>설사</Accordion.Header>
              <Accordion.Body>
                설사는 물기 많은 것을 먹었을때 일시적으로 보여질 수 있지만 스트레스나 장시간 이동시에도 보여질 수 있습니다. 혹은 사육온도가 낮을 경우, 먹이가 너무 차가울 경우, 식단에 섬유질 함유가 낮을 경우, 원충감염 등으로도 발생할 수 있습니다. 야채는 물을 잘 털어서주고 너무 차가울 때는, 전자랜지에 10초가량 돌리거나 잠시 상온에 두어 온도를 높일 것을 추천합니다.
                <br/><br/>
                변을 보았을 때 먹이의 원모습이 그대로 발견된 채 설사를 했다면 이는 저온에 의한 소화불량으로 볼 수 있습니다.
                <br/><br/>
                변에서 벌레가 보인다면 기생충 감염이 상당히 진행된 상태이므로 최대한 빨리 병원에 내원하고 최소 한달 이상의 구충이 필요합니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="5">
              <Accordion.Header>변비</Accordion.Header>
              <Accordion.Body>
                변비의 원인은 스트레스로 인한 대사불량, 탈수 , 이물질을 먹었을 때, 기생충으로 장관폐색 등이 있습니다.
                <br/><br/>
                변이 10일이상 나오지않는다면 일단 온욕을 시켜보는 것이 좋습니다. 수분보충을 하고 신진대사를 원활하게 하여 배변활동을 할 수도 있습니다.
                <br/><br/>
                그래도 변을 볼수없다면 임팩션이나 원충감염이 의심되므로 병원으로 가야합니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="6">
              <Accordion.Header>소변의 이상</Accordion.Header>
              <Accordion.Body>
                육겁의 소변은 무색입니다. 혹시나 소변에 피가 섞인 것 같다면 내부 장기의 문제이거나 결석으로 인해 뇨관이 손상되있을 가능성이 있습니다. 높은 곳에서 추락하여 내장기관이 다쳤다면 시급히 병원에 가야합니다.
                <br/><br/>
                소변에 악취가 난다면 이미 많이 진행된 탈수 이거나 간, 신장계 질환일 수 있습니다.
                <br/><br/>
                요산 배설형 거북의 경우 질소 대사 과정에서 아미노산의 질소화합물은 장기특이성(특정장기에반응)이 강해서 대부분 간에서 암모니아,요소,요산으로 분해가 됩니다. 이 암모니아는 간에서 대사된 것도 있고 혈중유리아미노산중 글루타민이 소장점막이 대사를 하여 방출된 암모니아가 대부분이기 때문에 이 소장에서 일어나는 위의 과정에 문제가 있을때 냄새가 날 수 있습니다. 혹은 탈수일 경우 요산이 부드러운 순두부형태가 아니라 몸 밖에 나올 때 굳은 모양으로 나오게 됩니다. 또한 요산 배설에 필요한 수분을 배설 과정에서 더욱 빼앗길 수 있기 때문에 탈수가 더 진행되거나 아예 탈수가 오게되면 요산배설을 할 수 없게되어 장막힘을 유발할 수 있습니다.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="7">
              <Accordion.Header>눈을 비빈다</Accordion.Header>
              <Accordion.Body>
                거북이가 눈을 계속 비빈다면 공기가 너무 건조하거나 환기가 잘 되지 않거나 눈 근처에 상처를 입었을 수 있습니다.
                <br/><br/>
                안약을 미리 구비해두고 온욕을 할 때 등 정기적으로 넣어주며 미리 눈병을 예방해주는 것이 좋습니다.
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
      </div>
    </div>

  )
};

export default Disease 