import React, {useState, useContext} from 'react';
// import {Card} from 'react-bootstrap';
import {useHistory, useParams} from 'react-router-dom';
import Data from './Data.js';
import './App.css';

function Feed(props){
    let [data, setdata] = useState(Data);
    // let i = 0;

    // const card_name = {data.name};

  return (
    <div className='feed _container'>
      <div className='inner'>            
        {data.map((a, i, variant)=>{
            return <Card data={data[i]} i={i} variant={variant}/>
          })
        }

      </div>
    </div>


  )
}

function Card(props){
  
  return(
    <div className={'card ' + props.data.state}>
        <div className='img_wrap'>
          <img src={'img/feed_'+ (props.i) +'.png'} alt=''/>
        </div>
        <div className='card-title'>{props.data.name}</div>
    </div>
  )
}

export default Feed